// Typography
$font-family: 'Lato', 'Helvetica Neue', Helvetica, sans-serif !default;
$heading-font-family: 'Lato', 'Helvetica Neue', Helvetica, sans-serif !default;
$monospace-font-family: monospace !default;

$base-font-size: 14px !default;
$base-font-viewport-small: 15px !default;
$base-font-viewport-medium: 16px !default;
$base-font-viewport-large: 16px !default;

$bio-name-font-size: 2rem !default;

$line-height: 1.5 !default;
$heading-line-height: 1.25 !default;

$bold-font-weight: bold !default;
$heading-font-weight: bold !default;

$h1: 1.5rem !default;
$h2: 1.2rem !default;
$h3: 1.1rem !default;
$h4: 1rem !default;
$h5: .875rem !default;
$h6: .75rem !default;

$abbr-border-bottom: 1px black dotted;

// Page width
$measure-width: 44rem !default;

// Viewport widths
$viewport-small: 32em;
$viewport-medium: 48em;
$viewport-large: 64em;

// Colors
$background-color: white;
$background-color-dm: #040813;


$blue: #0076df !default;
$dark-gray: #333 !default;
$light-gray: #ccc !default;
$new-tag-color: #FFD635 !default;
$info-tag-color: #47ccad !default;

// Links
$link-color: $blue;
$link-color-dm: #00b7ff;

// Whitespace
$space-1: .5rem !default;
$space-2: 1rem !default;
$space-3: 2rem !default;
$space-4: 4rem !default;

// Borders
$border-width: 2px !default;
$border-radius: 3px !default;