}

.last-updated {
	background-color: lighten($light-gray, 10%);
	color: lighten($dark-gray, 20%);
	font-style: italic;
	font-size: $h5;
	@media (prefers-color-scheme: dark) {
		background-color: lighten(#16181d, 10%);
		color: darken($light-gray, 10%);
  }
}

.info-tag {
	background-color: $info-tag-color;
	color: $dark-gray;
}